// header

body.landing {
  &:before,
  &:after {content: none;}

  header.banner {
    position: absolute;
    top: $vmargin;

    div.wrap {
      padding: 0 $gutter;
      @include bp-m {padding: 0 ($gutter*2);}

      p.logo {
        position: static;
        margin: 0 auto;
        width: 250px;
        background: url(../../assets/images/logos/inverted-mantra-systems.svg) no-repeat 50% 100%; 
        background-size: 100% auto;

        @include bp-m {margin: 0;}
        @include bp-l {width: 300px;}

        &:before {
          display: block;
          padding-top: 13%;
          content: "";
        }

        span {@include hide-screen();}
      }
    }
  }
}

// landing intro

div.landing-intro {
  padding: ($spacing*4) 0 0;

  @include bp-m {
    padding: ($spacing*4) 0 ($spacing*2);
    div.row {align-items: center;}
  }

  div.text {
    text-align: center;
    @include bp-m {text-align: left;}

    h1 {
      @extend .h1-2;
      margin-bottom: $vmargin/2;
    }

    p {
      @extend .lead;
      @include bp-m {margin-bottom: 0;}
    }
  }

  div.features {
    margin: 0 (-$gutter);
    padding: ($spacing*1.5) $spacing;
    background: rgba(white,0.1);
    text-align: center;

    @include bp-m {
      margin: $vmargin*2 0;
      border-radius: $border-radius;
      padding: $spacing*1.25;
    }

    @include bp-xl {padding: $spacing*2;}

    ul {
      display: inline-block;
      text-align: left;
      @include bp-m {font-size: $fs-22;}

      li {
        margin-bottom: 0.5rem;

        &:last-child {margin-bottom: 0;}
        &:before {margin-right: 0.5rem;}
      }
    }

    p {
      margin-bottom: 0;
      text-align: center;
    }
  }
}

// landing extra

div.landing-extra {
  text-align: center;

  @include bp-m {
    text-align: left;
    div.row {align-items: center;}
  }

  h2 {
    margin-left: auto;
    margin-right: auto;
    text-align: center;

    @include bp-m {
      margin: 0 auto ($vmargin*2);
      max-width: 700px;
      font-size: $fs-45;
    }
  }

  p.cta {margin-bottom: 0;}

  div.video-wrap {
    background: black;
    box-shadow: 0 0 10px 5px rgba(black,0.1);

    @include bp-m {margin-bottom: 0;}
  }
}

// landing content

div.landing-content {
  > h2:nth-of-type(1) {
    @include bp-m {
      font-size: $fs-45;
    }
  }

  > ul:not([class]):not([class]) {@extend .disc;}
  > ol:not([class]) {@extend .decimal;}
}
